import { Grid, Typography } from "@mui/joy";
import WorkExperienceCard from "./WorkExperienceCard";

export default function WorkExperience() {
    return (
        <Grid container gap={1} direction="column">
            <Typography level="h4">Опыт работы</Typography>
            <Grid container spacing={1}>
                <Grid item md={12} xs={12}>
                    <WorkExperienceCard
                        position={"Помощник преподавателя"}
                        company={"Центр олимпиадной подготовки \"Шмель\""}
                        startDate={"Сентябрь 2021"}
                        endDate={"Апрель 2022"}
                        tasks={["Помощь в проведении занятий по олимпиадной математике", "Помощь с проверкой заданий по олимпиадной математике"]}
                    />
                </Grid>
            </Grid>
        </Grid>
    )
}