import { Grid, Chip, Typography, Card } from "@mui/joy";
import GitHubIcon from '@mui/icons-material/GitHub';
import TelegramIcon from '@mui/icons-material/Telegram';

export default function () {
    return (
        <Grid container justifyItems="center" justifyContent="space-between" alignItems="center" gap={{md: 7, xs: 0}}>
            <Grid item>
                <Grid container direction="column" gap={1}>
                    <Grid item>
                        <Typography level="h1">Белоусов Дмитрий Анатольевич</Typography>
                        <Typography level="title-lg" textColor="neutral.600">Москва, Россия</Typography>
                    </Grid>
                    <Grid item>
                        <Grid container gap={1}>
                            <Chip href="mailto:beldmian@gmail.com" component="a" clickable variant="outlined" size="lg">beldmian@gmail.com</Chip>
                            <Chip href="tel:+79195010390" component="a" clickable variant="outlined" size="lg">+7 (919) 501 03 90</Chip>
                            <Chip
                                href="https://github.com/beldmian"
                                component="a" color="primary"
                                clickable
                                variant="solid"
                                size="lg"
                                target="_blank"
                                startDecorator={<GitHubIcon />}
                            >GitHub</Chip>
                            <Chip
                                href="https://t.me/beldmian"
                                component="a"
                                color="primary"
                                clickable
                                variant="solid"
                                size="lg"
                                target="_blank"
                                startDecorator={<TelegramIcon />}
                            >Telegram</Chip>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
            <Grid item sx={{display: { xs: "none", md: "block" }}}>
                <img src="/static/me.jpeg" alt="-" loading="lazy" style={{ height: "12rem", borderRadius: "0.75rem" }} />
            </Grid>
        </Grid>
    )
}