import { Typography, Grid } from "@mui/joy";
import EducationCard from "./EducationCard";

export default function Education() {
    return (
        <Grid container gap={1} direction="column">
            <Typography level="h4">Образование</Typography>
            <Grid container spacing={1}>
                <Grid item md={12} xs={12}>
                    <EducationCard
                        type={"Высшее, Бакалавр"}
                        name={"Национальный исследовательский университет «Высшая школа экономики» (НИУ ВШЭ)"}
                        profile={"Бизнес-информатика"}
                        startDate={2023}
                        endDate={2027}
                    />
                </Grid>
            </Grid>
        </Grid>
    )
}