import { Chip, Grid, Stack, Typography } from "@mui/joy";

export default function About() {
    return (
        <Grid container direction={"column"} gap={1}>
            <Typography level="h4">О себе</Typography>
            <Typography level="body-md" align="justify">Интересуюсь разработкой сложных, высоконагруженных систем, в частности разбираюсь в концепциях архитектуры ПО. В основном работаю с языком Go, однако также поверхностно знаю Python, C и Java. Вне IT также разбираюсь в математике, что помогает мне и в разработке.</Typography>
            <Typography level="h4">Ключевые навыки</Typography>
            <Stack direction={"row"} spacing={1} useFlexGap flexWrap="wrap">
                <Chip variant="soft" color="primary">Go</Chip>
                <Chip variant="soft" color="primary">Python</Chip>
                <Chip variant="outlined">HTTP</Chip>
                <Chip variant="outlined">Linux</Chip>
                <Chip variant="outlined">Git</Chip>
                <Chip variant="outlined">SQL</Chip>
                <Chip variant="outlined">PostgreSQL</Chip>
                <Chip variant="outlined">REST</Chip>
                <Chip variant="outlined">Docker</Chip>
                <Chip variant="outlined">Kubernetes</Chip>
                <Chip variant="outlined" color="success">Англйский язык (B2)</Chip>
            </Stack>
        </Grid>
    )
}